@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100%;
  margin: 0;
}


html, body, #app, #app>div {
    height: 100%;
    margin: 0;
  }
body {
    background-image: url(/connectfourai/static/media/lowpoly.80019574.png);
    background-color: #cccccc;
}
.background {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: middle;
            align-items: middle;
}
.connect-four {
    justify-self: middle;
    font-family: 'Lato', sans-serif;
    width: 50%;
    min-width: 500px;
    margin: auto;
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #0B0C10;
    box-shadow: 0px 13px 35px 0px rgba(0,0,0,0.75);
    border-radius: 2%;
}

.header {
    position: relative;
    z-index: 2;
}

.game-button {
    font-family: 'Roboto', sans-serif;
	background-color:#FC4445;
	border-radius:28px;
	border:1px solid #FC4445;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-size:20px;
	padding:5px 31px;
    margin-top: 20px;
	text-decoration:none;
}
.game-button:hover {
	background-color:white;
    color: #FC4445;
    cursor: pointer;
}

h1 {
    margin: 0;
    margin-top: 30px;
    font-size: 70px;
    text-align: center;
    color: #FC4445;
}

h2 {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    color: grey;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 40px;
    font-size: 20px;
    text-align: center;
}
.menu {
    position: relative;
    z-index: 2;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 10px;
    gap: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.menu p {
    font-family: 'Lato', sans-serif;
    padding: 0;
    padding-top: 20px;
    margin: 0;
    color: white;
}

.board {
    border: 5px solid #454545;
    border-radius: 2%;
    margin: 50px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    box-shadow:  15px 15px 27px #14161bb4, -15px -15px 27px #111318;
}

.col {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #949494;
    overflow: hidden;
}

.column-box {
    position: relative;
}
.col-border {
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    --box-shadow-color: #454545;
}
.col-border:hover {
    --box-shadow-color: #545454;
}
.cell-border {
    position: relative;
    width: 60px;
    height: 60px;
    margin: 0 auto;
    overflow: hidden;
}

.cell-border:after {
    content: '';
    position: absolute;
    left: 10px;
    top: 10px;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    box-shadow: 0 0 0 2000px var(--box-shadow-color);
    
}



.cell {
    width: 40px;
    height: 40px;
    margin: 10px;
    border-radius: 100%;
}



@-webkit-keyframes animateIn {
    from {top: -400px;}
    to {top: -1px;}
}



@keyframes animateIn {
    from {top: -400px;}
    to {top: -1px;}
}
.p1, .p2 {
    position: relative;
    width: 42px;
    height: 42px;
    top: -1px;
    left: -1px;
    border-radius: 100%;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: animateIn;
            animation-name: animateIn;
}
.p1 {
    background-color: #63469f;
}

.p2 {
    background-color: #2cbefa;
}
